.form-options {
    padding: 10px;
    margin: 0;
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 10px; 
    color: rgb(12, 110, 86);
}

.form-options label, .form-options label label {
    padding: 0;
    margin: 1px 0;
    display: inline;
    vertical-align: middle;
}

.form-options input[type = 'number']{
    padding: 2px;
    margin: 0;
    margin-left: 10px;
    border: 0.5px solid rgb(12, 110, 86, 0.2);
    color: rgb(12, 110, 86);
    width: 50px;
    border-radius: 2px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 10px; 
    color: rgb(12, 110, 86);
}
.form-options input[type = 'checkbox'] {
    margin: 0;
    padding: 0;
    margin-left: 10px;
    background-color: transparent;
    border: 0.5px solid rgb(12, 110, 86, 0.2);
    color: rgb(12, 110, 86);
    display: inline;
    vertical-align: middle;
}